import ChromeCanaryStack from './webrtc-stacks/ChromeCanaryStack'
import ChromeStableStack from './webrtc-stacks/ChromeStableStack'
import SafariStableStack from './webrtc-stacks/SafariStableStack'
import FcStack from './webrtc-stacks/FcStack'
import FirefoxStack from './webrtc-stacks/FirefoxStack'
import Logger from './utils/Logger'
import {ErrorCodes} from './utils/ErrorCodes';

var sessionId = 103;

var Connection = function (spec) {
  "use strict";
  var that = {};

  spec.session_id = (sessionId += 1);

  // Check which WebRTC Stack is installed.
  if (typeof window == "undefined" || !window.navigator) {
    Logger.error('Publish/subscribe video/audio streams not supported yet');
    that = FcStack(spec);
  } else if (window.navigator.userAgent.match("Firefox") !== null) {
    // Firefox
    that.browser = "mozilla";
    that = FirefoxStack(spec);
  } else if (window.navigator.userAgent.indexOf("Safari") > -1 && navigator.userAgent.indexOf("Chrome") === -1) {
    // Safari.
    Logger.debug("Safari");
    that = SafariStableStack(spec);
    that.browser = "safari";
  } else if (window.navigator.userAgent.indexOf("MSIE ")) {
    // Internet Explorer.
    //Logger.debug("IE");
    that = ChromeStableStack(spec);
    that.browser = "ie";
  } else if (window.navigator.appVersion.match(/Chrome\/([\w\W]*?)\./)[1] >= 26) {
    // Google Chrome Stable.
    //Logger.debug("Stable");
    that = ChromeStableStack(spec);
    that.browser = "chrome-stable";
  } else if (window.navigator.userAgent.toLowerCase().indexOf("chrome") >= 40) {
    // Google Chrome Canary.
    //Logger.debug("Canary!");
    that = ChromeCanaryStack(spec);
    that.browser = "chrome-canary";
  } else {
    // None.
    that.browser = "none";
    throw "WebRTC stack not available";
  }
  return that;
};

var electronUtil = function(config, callback, error) {
  var that = {};
  that.callback = callback;
  that.config = config;
  that.error = error;

  that.selectSource = function(sources, callback, error) {
    var dialog = document.createElement('div');
    dialog.setAttribute('style', 'position: absolute; z-index: 99999999; width: 65%; height: 70%; top:50%; left:50%; -webkit-transform:translate(-50%,-50%); transform:translate(-50%,-50%); border: #333333 1px solid; background: #ffffff; overflow: scroll;'
    + 'display: flex; justify-content: space-around; flex-wrap: wrap;'
    + 'padding: 50px 20px 5px 20px;box-shadow: 10px #333333;'
    );

    document.body.appendChild(dialog);

    

    sources.map(source => { 
      if(source.id) {
        var item = document.createElement('div');
        item.setAttribute('style','width: 30%;text-align: center;');

        var span = document.createElement('div');
        span.innerHTML = source.name;
        span.setAttribute('style','display: inline-block;width: 70%;word-break: keep-all;white-space: nowrap;overflow: hidden;text-overflow: ellipsis;');

        var imgContain = document.createElement('div');
        imgContain.setAttribute('style','width: 100%;height: 70%');

        var img = document.createElement('img');
        img.setAttribute('style','height: 100%;');
        img.src = source.thumbnail.toDataURL();

        item.onclick = () => {
          document.body.removeChild(dialog);
          callback && callback(source.id)
        }
        imgContain.appendChild(img);
        item.appendChild(imgContain);
        item.appendChild(span);
        dialog.appendChild(item);
      }
    })

    var bottom = document.createElement('button');
    bottom.innerHTML = 'Back';
    bottom.setAttribute('style', ' width: 100%; padding:10px 0; background-color: #fff; font-size: 20px; color: #333;border: none;')
    bottom.onclick = () => {
      document.body.removeChild(dialog);
      error && error('NotAllowedError');
    }
    dialog.appendChild(bottom);
  
  }

  that.share = function(electron) {
    electron.desktopCapturer.getSources({types: ['window', 'screen']}, (error, sources) => {
      if (error) throw error;
      that.selectSource(sources, function(sourceId) {

        var mWidth = that.config.attributes.width;
        var mHeight = that.config.attributes.height;
        var maxFr = that.config.attributes.maxFr;
        var minFr = that.config.attributes.minFr;

        var config = {
          audio: false,
          video: {
            mandatory: {
              chromeMediaSource: 'desktop',
              chromeMediaSourceId: sourceId,
              maxHeight: mHeight, 
              maxWidth: mWidth, 
              maxFrameRate: maxFr, 
              minFrameRate: minFr
            }
          }
        }

        navigator.webkitGetUserMedia(config, that.callback, that.error)
      }, that.error);
    })
  }
  return that;
}

var GetUserMedia = function (config, callback, error) {
  "use strict";
  
  navigator.getMedia = (navigator.getUserMedia ||
                        navigator.webkitGetUserMedia ||
                        navigator.mozGetUserMedia ||
                        navigator.msGetUserMedia);
  if (config.screen) {

    // for electron
    var electron = null;
    try {
      electron = window.require('electron');
    } catch (err) {}

    if (electron) {
      if(electron.desktopCapturer) {
        var el = electronUtil(config, callback, error);
        el.share(electron);
      } else {
        error(ErrorCodes.ELECTRON_NOT_SUPPORT_SHARING_SCREEN);
      }
      return;
    }
  
    Logger.debug("Screen access requested");
    if (window.navigator.userAgent.match("Firefox") !== null) {
      //firefox
      var mediaSource = ['screen', 'window', 'application'];
      if(!~mediaSource.indexOf(config.mediaSource)) {
        return error && error('Invalid mediaSource, mediaSource should be one of [screen, window, application]');
      }

      var theConfig = {};
      if(config.video.mandatory != undefined){
        theConfig.video = config.video;
        theConfig.video.mediaSource = config.mediaSource;
      } else {
        theConfig = { video: { mediaSource: config.mediaSource }};
      }
      navigator.getMedia(theConfig,callback,error);
    } else if (window.navigator.userAgent.match("Chrome") !==null) {
      //chrome
      if (window.navigator.appVersion.match(/Chrome\/([\w\W]*?)\./)[1] < 34) {
        error({code: "This browser does not support screen sharing"});
        return;
      }
      var extensionId = "okeephmleflklcdebijnponpabbmmgeo";
      if (config.extensionId){
        Logger.debug("extensionId supplied, using " + config.extensionId);
        extensionId = config.extensionId;
      }
      Logger.debug("Screen access on chrome stable, looking for extension");
      try {
        chrome.runtime.sendMessage(
            extensionId,
            { getStream: true },
            function (response) {
              if (response === undefined) {
                Logger.debug("Access to screen denied");
                var theError = {code: "Access to screen denied"};
                error(theError);
                return;
              }
              var theId = response.streamId;
              var mWidth = config.attributes.width;
              var mHeight = config.attributes.height;
              var maxFr = config.attributes.maxFr;
              var minFr = config.attributes.minFr;
              theConfig = {
                video: { mandatory:
                  { chromeMediaSource: 'desktop',  chromeMediaSourceId: theId, maxHeight: mHeight, maxWidth: mWidth, maxFrameRate: maxFr, minFrameRate: minFr}}};
              navigator.getMedia(theConfig,callback,error);
        });
      } catch (e) {
        Logger.debug("AgoraRTC screensharing plugin is not accessible");
        var theError = {code: "no_plugin_present"};
        error(theError);
        return;
      }
    } else {
      Logger.debug("This browser does not support screenSharing");
    }
  } else if (window.navigator.userAgent.indexOf("Safari") > -1 && navigator.userAgent.indexOf("Chrome") === -1) {
    navigator.mediaDevices.getUserMedia(config).then(callback).catch(error);
  } else {
    if (typeof navigator == 'undefined' || !navigator.getMedia) {
      Logger.error('Video/audio streams not supported yet');
    } else {
      navigator.getMedia(config, callback, error);
    }
  }
};

export {Connection, GetUserMedia}



