import GatewayClient from "./GatewayClient"
import Logger from "./utils/Logger"
import ErrorCodes from "./utils/ErrorCodes"
import {safeCall, generateSessionId, is32Uint} from "./Helper"
import {getGatewayList} from "./Server"
import {report} from "./Report2"
import {getBrowserOS, isSafari} from "./utils/Browser"
import {simMap} from "./SimMap"
import {Stream, getDevices} from "./Stream"
import {AUDIO_SAMPLE_RATE_48000, VIDEO_CODEC_PROFILE_HIGH} from "./GlobalName"
import * as config from "./Config"

const PUBLISHED = 0;
const PUBLISHING = 1;
const UNPUBLISHED = 2;

var Client = function (spec) {
  "use strict";

  var that = {};
  that.key = undefined;

  that.highStream = null;
  that.lowStream = null;
  that.lowStreamParameter = null;
  that.isDualStream = false;
  that.highStreamState = UNPUBLISHED;
  that.lowStreamState = UNPUBLISHED;

  that.setLowStreamParameter = function(lowStreamParameter) {
    that.lowStreamParameter = lowStreamParameter;
  }

  that._getVideoCameraIdByLabel = function(label, onSuccess, onFailure) {
    getDevices((devices) => {
      for(let device of devices) {
        if(device.label === label && device.kind === 'videoinput') {
          return onSuccess(device.deviceId);
        }
      }
      return onFailure();
    }, onFailure);
  } 

  that.init = function (appId, onSuccess, onFailure) {
    if(typeof appId !== 'string') {
      throw new Error('Param appId should be string');
    }

    Logger.info("Initializing AgoraRTC client");
    spec.appId = appId;
    spec.sessionId = generateSessionId();
    onSuccess && onSuccess();
  };

  that.configPublisher = function(config) {
    that.gatewayClient.configPublisher(config);
  };

  that.enableDualStream = function(onSuccess, onFailure) {
    if(getBrowserOS() === "iOS") {
      report.streamSwitch(spec.sessionId, {
        lts: new Date().getTime(),
        isdual: true,
        succ: false,
      })
      return onFailure && onFailure(ErrorCodes.IOS_NOT_SUPPORT);
    }

    report.streamSwitch(spec.sessionId, {
      lts: new Date().getTime(),
      isdual: true,
      succ: true,
    })

    that.isDualStream = true;
    if(that.highStreamState === PUBLISHED) {
      that._publishLowStream(onSuccess, onFailure);
    } else if(that.highStreamState === PUBLISHING) {
      onFailure && onFailure(ErrorCodes.STILL_ON_PUBLISHING);
    } else {
      onSuccess && onSuccess();
    }
  }

  that.disableDualStream = function(onSuccess, onFailure) {
    report.streamSwitch(spec.sessionId, {
      lts: new Date().getTime(),
      isdual: false,
      succ: true,
    });

    that.isDualStream = false;
    if(that.highStreamState === PUBLISHED) {
      that._unpublishLowStream(onSuccess, onFailure);
    } else if(that.highStreamState === PUBLISHING) {
      onFailure && onFailure(ErrorCodes.STILL_ON_PUBLISHING);
    } else {
      onSuccess && onSuccess();
    }
  }

  that._createLowStream = function(onSuccess, onFailure) {
    if(!that.highStream || !that.highStream.stream) {
      return Logger.warning("Can't create low-stream without highStream");
    }

    var spec = Object.assign({}, that.highStream.params);
    spec.streamID += 1;
    spec.audio = false;

    if(!spec.video) {
      return;
    }
    
    var videoTrack = that.highStream.stream.getVideoTracks()[0];
    if(!videoTrack) {
      onFailure('can not get hight-stream video track');
    }

    that._getVideoCameraIdByLabel(videoTrack.label ,function(deviceId) {
      spec.cameraId = deviceId;
      var lowStream = new Stream(spec);
      lowStream.streamId = that.highStream.getId() + 1;
      if(that.lowStreamParameter) {
        var parameter = Object.assign({}, that.lowStreamParameter);
        if(!parameter.width || !parameter.height) {
          var sim = simMap(that.highStream.profile)
          var res = config.SUPPORT_RESOLUTION_LIST[sim[0]];
          parameter.width = res[0];
          parameter.height = res[1];
        }
        parameter.framerate = parameter.framerate || 5;
        parameter.bitrate = parameter.bitrate || 50;

        if(isSafari()) {
          var res = config.SUPPORT_RESOLUTION_LIST[that.highStream.profile];
          parameter.width = res[0];
          parameter.height = res[1];
        }
        lowStream.setVideoProfileCustomPlus(parameter);
      } else {
        lowStream.setVideoProfileCustom(simMap(that.highStream.profile));
      }
      lowStream.init(() => {
        that.highStream.lowStream = lowStream;
        if(!videoTrack.enabled) {
          lowStream.disableVideo();
        }
        onSuccess && onSuccess(lowStream);
      }, () => onFailure('can not get hight-stream video track'));
    }) 
  }

  that._getLowStream = function(onSuccess, onFailure) {
    if(that.lowStream) {
      onSuccess(that.lowStream)
    } else {
      that._createLowStream(lowStream => {
        that.lowStream = lowStream;
        onSuccess(that.lowStream)
      }, onFailure);
    }
  }

  that._publishLowStream = function(onSuccess, onFailure) {
    if(that.lowStreamState !== UNPUBLISHED) {
      Logger.warning("Can't publish low-stream when low-stream already publish");
      return onFailure && onFailure();
    }

    if(that.highStream && that.highStream.hasScreen()) {
      return onFailure && onFailure(ErrorCodes.SHARING_SCREEN_NOT_SUPPORT);
    }

    that._getLowStream(lowStream => {
      that.lowStreamState = PUBLISHING;
      that.gatewayClient.publish(lowStream, () => {
        that.lowStreamState = PUBLISHED;
        onSuccess && onSuccess();
      }, (e) => {
        Logger.debug("publish low stream failed");
        onFailure && onFailure(e);
      });
    }, onFailure)
    
  }

  that._unpublishLowStream = function(onSuccess, onFailure) {
    if(that.lowStreamState !== PUBLISHED) {
      Logger.warning("Can't unpublish low-stream when low-stream not ever publish");
      return onFailure && onFailure();
    }

    if(that.lowStream) {
      that.gatewayClient.unpublish(that.lowStream, () => {}, (e) => {
          Logger.debug("unpublish low stream failed");
          onFailure && onFailure(e);
        }
      );
      that.lowStream.close();
      that.lowStream = null;
      that.lowStreamState = UNPUBLISHED;
      onSuccess && onSuccess();
    }

  }

  that.join = function(channelKey, channel, uid, onSuccess, onFailure) {
    if(channelKey && typeof channelKey !== 'string') {
      return onFailure && onFailure('Param channelKey should be string');
    }

    if(typeof channel !== 'string') {
      return onFailure && onFailure('Param channel should be string');
    }

    if(uid && (typeof uid !== 'number' || !is32Uint(uid))) {
      return onFailure && onFailure('Param uid should be number');
    }

    that.highStream = null;
    that.lowStream = null;
    that.lowStreamParameter = null;
    that.isDualStream = false;
    that.highStreamState = UNPUBLISHED;
    that.lowStreamState = UNPUBLISHED;

    var joinInfo = {
      appId: spec.appId,
      sid: spec.sessionId,
      cname: channel,
      uid: uid
    }

    // sessionInit event report
    report.sessionInit(spec.sessionId, {
      lts: new Date().getTime(),
      cname: channel,
      appid: spec.appId,
      mode: spec.mode,
      succ: true,
    })
    
    getGatewayList(joinInfo, function(res) {
      Logger.info("Joining channel: " + channel);
    
      // TODO delete if useless
      if (channelKey) {
        that.key = channelKey;
      } else {
        that.key = spec.appId;
      }

      joinInfo.cid = res.cid;
      joinInfo.uid = res.uid;
      joinInfo.gatewayAddr = res.gateway_addr; 
      that.joinInfo = joinInfo;

      that.gatewayClient.join(joinInfo, that.key, function(uid) {
        Logger.info("Join channal " + channel + " success");
        onSuccess(uid);
      }, onFailure);
    }, onFailure);
  };

  that.renewChannelKey = function(channelKey, onSuccess, onFailure) {
    if (that.key === undefined) {
      Logger.error("renewChannelKey should not be called before user join");
      safeCall(onFailure, ErrorCodes.INVALID_OPERATION);
    }
    that.key = channelKey;
    safeCall(onSuccess);
  };

  that.leave = function(onSuccess, onFailure) {
    Logger.info("Leaving channel");
    that.gatewayClient.leave(onSuccess, onFailure);
  };

  that._publish = function (stream,  onSuccess, onFailure) {
    if(that.highStreamState !== UNPUBLISHED) {
      Logger.warning("Can't publish stream when stream already publish", stream.getId());
      return onFailure && onFailure("stream already publish");
    }

    Logger.info("Publishing stream, uid: " , stream.getId());
    that.highStream = stream;
    that.highStreamState = PUBLISHING;
    that.highStream.streamId = that.joinInfo.uid;

    that.gatewayClient.publish(stream, function() {
      stream.sid = spec.sessionId;
      that.highStreamState = PUBLISHED;
      Logger.info("Publish success, uid:", stream.getId());
      if(that.isDualStream) {
        that._publishLowStream(() => {
          onSuccess && onSuccess();
        }, onFailure);
      } else {
        onSuccess && onSuccess()
      }
    }, onFailure); 
    
  };

  that._unpublish = function (stream, onSuccess, onFailure) {
    if(that.highStreamState !== PUBLISHED) {
      Logger.warning("Can't unpublish stream when stream not publish");
      return onFailure && onFailure("stream not publish");
    }

    Logger.info("Unpublish stream, uid: " , stream.getId());

    if(that.isDualStream && that.lowStream) {
      that._unpublishLowStream(null, onFailure)
      that.gatewayClient.unpublish(stream, null, onFailure)
      that.highStreamState = UNPUBLISHED;
      Logger.info("Unpublish stream success, uid:", stream.getId());

    } else {
      that.gatewayClient.unpublish(stream, null, onFailure);
      that.highStreamState = UNPUBLISHED;
      Logger.info("Unpublish stream success, uid:", stream.getId());
    }
    onSuccess && onSuccess();
  };

  that.publish = function (stream, onFailure) {
    if(that.highStreamState !== UNPUBLISHED) {
      onFailure && onFailure('stream already published');
    }
    that._publish(stream, null, onFailure);
  }

  that.unpublish = function (stream, onFailure) {
    if(that.highStreamState !== PUBLISHED) {
      onFailure && onFailure('stream not yet published');
    }
    that._unpublish(stream, null, onFailure);
  }

  that.subscribe = function (stream, onFailure) {
    Logger.info("Subscribe stream, uid: " , stream.getId());
    that.gatewayClient.subscribe(stream, null, onFailure);
  };

  that.unsubscribe = function (stream, onFailure) {
    Logger.info("Unsubscribe stream, uid: " , stream.getId());
    that.gatewayClient.unsubscribe(stream, null, onFailure);
  };
  
  that.setRemoteVideoStreamType = function (stream, streamType) {
    that.gatewayClient.setRemoteVideoStreamType(stream, streamType);
  };

  that.startLiveStreaming = function (url, transcodingEnabled) {
    that.gatewayClient.startLiveStreaming(url, transcodingEnabled);
  };

  that.stopLiveStreaming = function (url) {
    that.gatewayClient.stopLiveStreaming(url);
  };

  that.setLiveTranscoding = function (transcoding) {
    Object.assign(LiveTranscoding, transcoding);
    that.gatewayClient.setLiveTranscoding(LiveTranscoding);
  };

  that.gatewayClient = GatewayClient(spec);
  that.on = that.gatewayClient.on;

  that.on("rejoin", () => {
    // won't republish if preState = UNPUBLISHED, and PUBLISHING as PUBLISHED;
    let preState = that.highStreamState === UNPUBLISHED ? UNPUBLISHED : PUBLISHED;
    
    if(that.highStream && preState === PUBLISHED) {
      Logger.info("publish after rejoin");
      that.highStreamState = UNPUBLISHED;
      that.lowStreamState = UNPUBLISHED;

      that.publish(that.highStream, e => {
        e && Logger.info(e);
      });
    }
  })

  that.on("pubP2PLost", evt => {
    Logger.debug('Start reconnect local peerConnection :', that.highStream.getId());
    that.gatewayClient.dispatchEvent({type: "stream-reconnect-start", id: that.highStream.getId()});
    that._unpublish(that.highStream, () => {
      that._publish(that.highStream, () => {
        Logger.debug('Reconnect local peerConnection success:', that.highStream.getId());
        that.gatewayClient.dispatchEvent({type: "stream-reconnect-end", id: that.highStream.getId()});
      }, err => {
        Logger.debug('Reconnect local peerConnection failed:' + err);
        that.gatewayClient.dispatchEvent({type: "stream-reconnect-end", id: that.highStream.getId()});
      });
    }, err => {
      Logger.debug('Reconnect local peerConnection failed:' + err);
      that.gatewayClient.dispatchEvent({type: "stream-reconnect-end", id: that.highStream.getId()});
    });
  });

  that.on("subP2PLost", evt => {
    Logger.debug('Start reconnect remote peerConnection :', evt.stream.getId());
    that.gatewayClient.dispatchEvent({type: "stream-reconnect-start", id: evt.stream.getId()});
    that.gatewayClient.unsubscribe(evt.stream, () => {
      that.gatewayClient.subscribe(evt.stream, () => {
        Logger.debug('Reconnect remote peerConnection success:', evt.stream.getId());
        that.gatewayClient.dispatchEvent({type: "stream-reconnect-end", id: evt.stream.getId()});
      }, err => {
        Logger.debug('Reconnect remote peerConnection failed:' + err);
        that.gatewayClient.dispatchEvent({type: "stream-reconnect-end", id: evt.stream.getId()});
      });
    }, err => {
      Logger.debug('Reconnect remote peerConnection failed:' + err);
      that.gatewayClient.dispatchEvent({type: "stream-reconnect-end", id: evt.stream.getId()});
    });
  });

  return that;
};

var TranscodingUser = {
  uid: 0,
  x: 0,
  y: 0,
  width: 0,
  height: 0,
  zOrder: 0,
  alpha: 1.0,
};

var LiveTranscoding = {
  width: 640,
  height: 360,
  videoBitrate: 400,
  videoFramerate: 15,
  lowLatency: false,
  audioSampleRate: AUDIO_SAMPLE_RATE_48000,
  audioBitrate: 48,
  audioChannels: 1,
  videoGop: 30,
  videoCodecProfile: VIDEO_CODEC_PROFILE_HIGH,
  userCount: 0,
  userConfigExtraInfo: {},
  backgroundColor: 0x000000,
  transcodingUsers: [],
};

var createClient = function(options) {
  if (options && options.mode === "interop") {
    Logger.info("Creating client , MODE : Interop");
    return Client({mode: "live"});
  } else if (options && options.mode === "h264_interop") {
    Logger.info("Creating client , MODE : h264_interop");
    return Client({mode: "live", codec: "h264"});
  } else if (options && options.mode === "web-only") {
    Logger.info("Creating client , MODE : web-only");
    return Client({mode: "rtc"});
  } else {
    throw new Error('Invalid mode');
  }
};

var createRtcClient = function() {
  Logger.info("Creating client , MODE : rtc");
  Logger.warning("createRtcClient is deprecated.");
  return Client({mode: "rtc"});
};

var createLiveClient = function(spec) {
  var id = "host";
  if (spec && spec.role === "audience") {
    id = spec.role;
  }
  Logger.info("Creating client , MODE : live");
  Logger.warning("createLiveClient is deprecated.");
  return Client({mode: "live", role: id});
};

export {
  createClient,
  createRtcClient,
  createLiveClient,

  TranscodingUser,
  LiveTranscoding
}



